import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, ToastController } from 'ionic-angular';

import { BarcodeScanner } from '@ionic-native/barcode-scanner';

import { LoginPage } from "../login/login";
import { PrincipalPage } from "../principal/principal";
import { HttpClient } from '@angular/common/http';

import firebase from "firebase";

@IonicPage()
@Component({
  selector: 'page-cuenta',
  templateUrl: 'cuenta.html',
})
export class CuentaPage {

  public estado = "ocultar"

  public rate;
  public textoDelBoton;
  public estadoBoton: boolean = false;
  public ocultarSpinner: boolean = false;
  public textoRate;
  public propina;
  public propinaTotal;
  public subTotal;
  public total;
  public pedidos: Array<any>;

  public firebase = firebase;
  public usuario: any;
  public mesa;
  public keyCliente;

  public ocultarAlert: boolean = true;
  public alertTitulo;
  public alertMensaje;
  public alertMensajeBoton;
  public alertHandler;
  public alertMostrarBotonCancelar = true;
  public esDelivery: boolean;
  public descuento;

  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    private barcodeScanner: BarcodeScanner,
    private toastCtrl: ToastController, public httpClient: HttpClient) {

    console.clear();

    this.rate = 1;
    this.textoDelBoton = "pagar";
    this.textoRate = "Malo";
    this.propina = 0;
    this.propinaTotal = 0;
    this.subTotal = 0;
    this.total = 0;
    this.pedidos = [];
    this.esDelivery = false;

    let usuariosRef = this.firebase.database().ref("usuarios");
    this.usuario = JSON.parse(localStorage.getItem("usuario"));
    this.mesa = "";

    usuariosRef.once("value", (snap) => {

      let data = snap.val();

      for (let item in data) {

        if (data[item].correo == this.usuario.correo) {

          this.mesa = data[item].mesa;
          this.keyCliente = item;
          break;
        }
      }
    }).then(() => {
      let keyPedido = "";
      if(this.mesa == null || this.mesa == ""){
        keyPedido = this.usuario.correo.replace("@","").replace(".","");
        this.esDelivery = true;
      }else{
        keyPedido = this.mesa;
      }

      let pedidoRef = this.firebase.database().ref("pedidos").child(keyPedido);

      pedidoRef.once("value", (snap) => {

        let data = snap.val();

        for (let item in data) {

          for (let subItem in data[item]) {

            if (typeof (data[item][subItem]) != "string") {

              this.pedidos.push(data[item][subItem]);
            }
          }
        }

        for(let item of this.pedidos){
          this.subTotal += item.precio * item.cantidad;
        } 
        this.total = this.subTotal;
     /*   this.total = this.subTotal = this.pedidos.reduce((valorAnterior, valorActual, indice) => {

          if (indice > 1){
            return valorAnterior.precio + valorActual.cantidad 
            * valorActual.precio;
          }
          else{
            return valorAnterior.cantidad * valorAnterior.precio 
            + valorActual.cantidad * valorActual.precio;
          }
        });*/
        console.log("TOTAL 1: ", this.total);

        if (data.desc) {
          this.descuento = this.subTotal * 0.1;
          this.total = this.subTotal -= this.descuento;
        }

        console.log("TOTAL 2: ", this.total);
        this.estado = "cuenta";
        this.ocultarSpinner = true;

      }).catch(() => this.presentToast("Ups... Tenemos problemas tcnicos."));
    }).catch(() => this.presentToast("Ups... Tenemos problemas tcnicos."));
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad CuentaPage');
  }

  Votar() {

    switch (this.rate) {
      case 1:
        this.propina = 0;
        this.textoRate = "Malo";
        break;

      case 2:
        this.propina = 5;
        this.textoRate = "Regular";
        break;

      case 3:
        this.propina = 10;
        this.textoRate = "Bien";
        break;

      case 4:
        this.propina = 15;
        this.textoRate = "Muy bien";
        break;

      case 5:
        this.propina = 20;
        this.textoRate = "Excelente";
        break;

      default:
      this. propina = 0;
        this.textoRate = "Hola";
        break;
    }

    //this.propinaTotal = (this.subTotal * this.propina) / 100;
    this.total = this.subTotal + ((this.subTotal * this.propina) / 100);
        console.log("TOTAL 3: ", this.total);

    if (this.rate > 1 && !this.esDelivery)
      this.textoDelBoton = "Verificar mesa";
    else
      this.textoDelBoton = "Pagar"

  }

  Pagar() {

    if (this.textoDelBoton == "Verificar mesa") {

      let options = { prompt: "Verific tu mesa para dar tu propina.", formats: "QR_CODE" };

      this.barcodeScanner.scan(options).then(barcodeData => {

        if (barcodeData.text == this.mesa)
          this.textoDelBoton = "Pagar";
        else
          this.presentToast("Ese QR no pertenece a tu mesa.")


      }).catch(err => { });

    } else {

      let keyPedido = "";
      let estadoPedido = "pagando";
      if(this.mesa == null || this.mesa == ""){
        keyPedido = this.usuario.correo.replace("@","").replace(".","");
        estadoPedido = "pagandoDelivery";
      }else{
        keyPedido = this.mesa;
      }
      let clienteRef = this.firebase.database().ref("usuarios").child(this.keyCliente);
      let pedidoRef = this.firebase.database().ref("pedidos").child(keyPedido);

      this.estadoBoton = true;
      this.ocultarSpinner = false;

      pedidoRef.update({estado: estadoPedido}).then(()=>{
        clienteRef.update({estado: estadoPedido, cuenta: this.total}).then(()=>{
              this.MostrarAlert("xito!", "Aguarde la confirmacin, nos ayudara mucho que completases una encuesta sobre tu experiencia en el lugar.", "Ok", this.Redireccionar);
              this.ocultarSpinner = true;

              this.httpClient.get("http://wirepusher.com/send?id=mpgtP&title=COMANDA&message=Un cliente ha pagado su cuenta")
              .subscribe(data => {
                console.log(data['_body']);
               }, error => {
                console.log(error);
              });
        
              this.httpClient.get("http://wirepusher.com/send?id=mpgtm&title=COMANDA&message=Un cliente ha pagado su cuenta")
              .subscribe(data => {
                console.log(data['_body']);
               }, error => {
                console.log(error);
              });

        }).catch(() => this.presentToast("Ups... Tenemos problemas tcnicos."));
      }).catch(() => this.presentToast("Ups... Tenemos problemas tcnicos."));

      /*pedidoRef.remove().then(() => {

        clienteRef.child("estado").update({estado: "pago"}).then(() => {

          clienteRef.child("comensales").remove().then(() => {

            clienteRef.child("juegoFer").remove().then(() => {

              clienteRef.child("juegoFacu").remove().then(() => {

                clienteRef.child("juegoAxel").remove().then(() => {

                  clienteRef.child("mesa").remove().then(() => {

                    mesaRef.once("value", (snap) => {

                      let data = snap.val();

                      for (let item in data) {

                        if (data[item].numeroMesa == this.mesa) {

                          mesaRef.child(item).update({ estado: "libre" }).then(() => {

                            mesaRef.child(item).child("cliente").remove().then(() => {

                              mesaRef.child(item).child("tiempoMinimo").remove().then(() => {

                                this.MostrarAlert("xito!", "Gracias por comer en nuestro restaurante, nos ayudara mucho que completases una encuesta sobre tu experiencia en el lugar.", "Ok", this.Redireccionar);
                                this.ocultarSpinner = true;
                              }).catch(() => this.presentToast("Ups... Tenemos problemas tcnicos."));
                            }).catch(() => this.presentToast("Ups... Tenemos problemas tcnicos."));
                          }).catch(() => this.presentToast("Ups... Tenemos problemas tcnicos."));;

                          break;
                        }
                      }
                    }).catch(() => this.presentToast("Ups... Tenemos problemas tcnicos."));
                  }).catch(() => this.presentToast("Ups... Tenemos problemas tcnicos."));
                }).catch(() => this.presentToast("Ups... Tenemos problemas tcnicos."));
              }).catch(() => this.presentToast("Ups... Tenemos problemas tcnicos."));
            }).catch(() => this.presentToast("Ups... Tenemos problemas tcnicos."));
          }).catch(() => this.presentToast("Ups... Tenemos problemas tcnicos."));
        }).catch(() => this.presentToast("Ups... Tenemos problemas tcnicos."));
      }).catch(() => this.presentToast("Ups... Tenemos problemas tcnicos."));*/
    }
  }

  presentToast(mensaje: string) {

    let toast = this.toastCtrl.create({
      message: mensaje,
      duration: 3000,
      position: 'top',
      cssClass: "infoToast"
    });

    toast.present();
  }

  MostrarAlert(titulo: string, mensaje: string, mensajeBoton: string, handler) {
    this.ocultarAlert = false;
    this.alertTitulo = titulo;
    this.alertMensaje = mensaje;
    this.alertMensajeBoton = mensajeBoton;
    this.alertHandler = handler;
  }

  Redireccionar() {
    this.navCtrl.setRoot(PrincipalPage);
  }

  Logout() {

    let usuariosRef = this.firebase.database().ref("usuarios");

    usuariosRef.once("value", (snap) => {

      let data = snap.val();

      for (let item in data) {

        if (data[item].correo == this.usuario.correo) {

          usuariosRef.child(item).update({
            logueado: false
          }).then(() => {
            if (this.usuario.tipo == "mozo"
              || this.usuario.tipo == "cocinero"
              || this.usuario.tipo == "bartender"
              || this.usuario.tipo == "metre"
              || this.usuario.tipo == "repartidor") {

              // Para redireccionar a la encuesta de axel.
              // localStorage.setItem("desloguear", "true");
              // this.navCtrl.setRoot(EncuestaDeEmpleadoPage);

              localStorage.clear();
              this.navCtrl.setRoot(LoginPage);
            } else {

              localStorage.clear();
              this.navCtrl.setRoot(LoginPage);
            }
          });

          break;
        }
      }
    });
  }

}



